import React from 'react';
import { Link } from 'react-router-dom';

type HListObject = {
    image : string,
    path : string,
    store : string,
    price : number
}

type HListParams = {
    items : HListObject[]
}

function HList({items} : HListParams){

    return (
        <ul className="list-group list-group-horizontal overflow-scroll">
            {items && items.map((item, key) => (
                 <li className="list-group-item border-0 ps-0 py-0" key={key}>
                     <HListItem {...item} /> 
                 </li>
            ))}
        </ul>
    )
}

const priceLineHegiht : React.CSSProperties =  {
    verticalAlign:'middle',
}

function HListItem({...props} : HListObject){
    return (
        <Link to={props.path}>
            <figure className="figure position-relative">
                <img src={props.image} className="img_fluid rounded" />
                {/* <figcaption className="figure-caption text-light position-absolute px-3 d-flex flex-row pb-3 w-100" style={{top: '0px',height:'160px'}}>
                    <div className="fs-4 text-wrap align-self-end" style={{flex:1}}>
                        {props.store}
                    </div>
                    <div className="fs-6 align-self-end text-end" style={{flex:1}}>
                        <span style={{...priceLineHegiht}}>1</span>&nbsp;
                        <span className="fs-4 fw-700" style={{fontFamily:'Roboto', ...priceLineHegiht}}>{props.price.toLocaleString()}</span>
                        <span className="fw-300" style={{...priceLineHegiht}}> </span>
                    </div>
                </figcaption> */}
            </figure>
        </Link> 
    )
}

export { HList };