import React from 'react';
import { Form } from 'react-bootstrap';

type CommonFormGroupParams = {
    label : string,
    placeholder : string
}

function CommonFormGroup({label, placeholder} : CommonFormGroupParams){
    return (
        <Form.Group>
            <Form.Label style={{fontSize:'12px', marginBottom:'4px'}}>{label}</Form.Label>
            <Form.Control style={{padding:'13px', fontSize:'16px', fontWeight:500, background:'#F6F7FF', borderRadius:'15px', border:'1px solid #E7EAF8'}} type="text" placeholder={placeholder}></Form.Control>
        </Form.Group>
    )
}

function CommonControl({placeholder}) {
    return (
        <Form.Control style={{padding:'13px', fontSize:'16px', fontWeight:500, background:'#F6F7FF', borderRadius:'15px', border:'1px solid #E7EAF8'}} type="text" placeholder={placeholder}></Form.Control>
    )
}

export {
    CommonFormGroup,
    CommonControl
}