import React from 'react';
import { Container } from 'react-bootstrap';

const ContainerStyle : React.CSSProperties = {
    paddingInline : '24px'
}


function MeetContainer ({...props}){

    return (
        <Container {...props} style={{...ContainerStyle, ...props.style}} >
            {props.children}
        </Container>
    )
}

const SubHeaderStyle : React.CSSProperties = {
    
}

function SubHeader ({...props}){

    return (
        <div {...props} className={props.className} style={{...SubHeaderStyle, ...props.style}}>
            {props.children}
        </div>
    )
}

const WrapperStyle : React.CSSProperties = {
    paddingTop : '24px'
}

function Wrapper ({...props}){
    return (
        <div {...props} style={{...WrapperStyle, ...props.style}}>
            {props.children}
        </div>
    )
}

const StoreContentStyle : React.CSSProperties = {
    marginInline: '16px',
    borderRadius: '20px',
    backgroundColor: 'white',
    fontSize: '14px',
}

function StoreContent({...props}){
    return (
        <div {...props} style={{...StoreContentStyle, ...props.style}}>
            {props.children}
        </div>
    )
}

export {
    MeetContainer,
    SubHeader,
    Wrapper,
    StoreContent
}
