import React, { useState } from 'react';
import { Link } from 'react-router-dom';

type CategoryParam = {
    icon : string,
    title : string,
    path : string
}

function Category ({icon, title, path} : CategoryParam) {
    
    return (
        <div className="text-center align-middle">
            <Link to={path}>
                <img src={icon} className="img-fluid" style={{borderRadius:'10px'}} />
            </Link>
            <div className="text-center" style={{color:'#5C5C64', paddingTop:'10px'}}>{title}</div>
        </div>
    )
}

export { Category };