import React, { useEffect, useState } from 'react';
import {Outlet, useLocation, useNavigate } from 'react-router-dom';
import { BottomNavigationParams, Navigation, BottomNavigation, BottomNavigationObject } from '../pieces/layouts/navigation';

const BottomNavigationArray : BottomNavigationObject[] = 
    [
        {image : '/icons/icon_home.png', text : '', path : '/home'},
        {image : '/icons/icon_search.png', text : '', path : '/search'},
        {image : '/icons/icon_around.png', text : '', path : '/around'},
        {image : '/icons/icon_favorite_black.png', text : '', path : '/favorite'},
        {image : '/icons/icon_mypage.png', text : '', path: '/mypage'}
    ];

function Root() {
    const [boot, setBoot] = useState(false);
    const location = useLocation();
    const navigate = useNavigate();

    useEffect(()=>{
        if((location.pathname == "/" || location.pathname == "") && !boot){
            console.log(location.pathname);
            setBoot(true);
            navigate("/home");
        }
    }, [location.pathname]);
    
    return (
        <div style={{marginBottom:'100px'}}>
            {/*   */}
            {/*  */}
            <main className="main">
                <Outlet />
            </main>
            {/*   */}
            <div className="container">
                <BottomNavigation items={BottomNavigationArray} />
            </div>
        </div>
    );
}
export { Root };