import React from 'react';

const ContentHeaderStyle : React.CSSProperties = {
    fontWeight : 700
}

/* 
  
*/
function ContentHeader ({...props}){

    return (
        <h2 {...props} style={{...ContentHeaderStyle, ...props.style}}>
            {props.children}
        </h2>
    )
}

/*
    inline 
*/

const ContentSubHeaderStyle : React.CSSProperties = {
    fontSize:'16px',
    fontWeight:700, 
    color:"#838186",
    marginBottom:'10px'
}

function ContentSubHeaderInline({...props}){
    return (
        <span {...props} style={{...ContentSubHeaderStyle, ...props.style}}>
            {props.children}
        </span>
    )
}

/*
    block
*/
function ContentSubHeaderBlock({...props}){
    return (
        <div {...props} style={{...ContentSubHeaderStyle, ...props.style}}>
            {props.children}
        </div>
    )
}

/*
   
*/
function ContentSub({...props}){
    return (
        <div {...props} style={{marginTop:'24px', ...props.style}}>
            {props.children}
        </div>
    )
}

export {
    ContentHeader,
    ContentSubHeaderInline,
    ContentSubHeaderBlock,
    ContentSub,
};