import React, {useContext, useState} from 'react';
import { Button, Image, InputGroup, Accordion, useAccordionButton, Dropdown, ButtonGroup } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import { Address } from '../../pages/Search';

import './Searchs.css';

type SearchBarParam = {
    style? : React.CSSProperties,
    preImage? : string,
    label : string
}

const searchTxtCss : React.CSSProperties = {
    backgroundColor : '#F6F7FF',
    borderInlineWidth : 0,
    borderColor: '#E7EAF8',
    paddingBlock:'16px',
    fontSize:'16px',
    lineHeight:1.5,
    borderRadius:'0 0 0 0'
}

const searchTxtCloseCss : React.CSSProperties = {
    borderLeftWidth:0, 
    borderColor: '#E7EAF8',
    backgroundColor : '#F6F7FF',
    borderRadius:'0 15px 15px 0'
}

const searchTxtPreCss : React.CSSProperties = {
    backgroundColor : '#F6F7FF',
    borderColor: '#E7EAF8',
    borderRightWidth:0,
    borderRadius:'15px 0 0 15px'
}

function SearchBar({style, preImage, label} : SearchBarParam){

    const [searchWord, setSearchWord] = useState('');

    const resetText = () => {
        setSearchWord('');
    }

    return(
        <InputGroup style={style} size="lg">
            <Button variant="{outline-secondary" style={searchTxtPreCss}>
                <Image src={preImage} />
            </Button>
            <Form.Control placeholder={label} value={searchWord} onChange={(event)=>{setSearchWord(event.target.value)}} style={searchTxtCss} />
            <Button variant={"outline-secondary"} className="align-middle" style={searchTxtCloseCss} onClick={resetText}>
                <Image src={'/btns/btn_search_cancel.png'} />
            </Button>
        </InputGroup>
    );
}

type KeyWordParam = {
    items : Address[]
}

const KeywordCss : React.CSSProperties = {
    gridTemplateColumns : '1fr 1fr'
}

const toggledCollapseContext = React.createContext(999);

const collapseStyle : React.CSSProperties = {
    display : 'flex',
    flexWrap : 'wrap',
    width : '200%'
}

function KeyWord({items} : KeyWordParam){
    const [activeCollapse, setActiveCollapse] = useState(999);

    const collapsedEvent = (activeCollapseKey : number) => {
        setActiveCollapse(activeCollapseKey);
    }

    return (
        <toggledCollapseContext.Provider value={activeCollapse}>
            <Accordion className="d-grid" style={KeywordCss}>
            {items && items.map((item, key)=> (
                <div key={key} className="d-grid">
                    <KeyWordItem collapsedEvent={collapsedEvent} address={item} keyNumber={key} />
                    <div style={key % 2 == 0 ? collapseStyle : {...collapseStyle, position:'relative', left:'-100%'}}>
                        <Accordion.Collapse className="w-100" eventKey={key.toString()}>
                            <div className="d-grid" style={KeywordCss}>
                            { item.City && item.City.map((value, index)=>(
                                <div key={index}>
                                    {value}
                                </div>
                            ))}
                            </div>
                        </Accordion.Collapse>
                    </div>
                </div>
            ))}
            </Accordion>
        </toggledCollapseContext.Provider>
    )
}

type KeyWordItemParam = {
    address : Address,
    keyNumber : number,
    style? : React.CSSProperties
    collapsedEvent : Function
}

const keywordItemCss : React.CSSProperties = {
    fontSize: '16px',
    fontWeight : 500,
}


type CustomToggleParam = {
    children : JSX.Element,
    eventKey : string,
    className? : string,
    style? : React.CSSProperties,
    collapsedEvent : Function
}


function CustomToggle({ children, eventKey, className, style, collapsedEvent } : CustomToggleParam) {
    const toggledCollpase = useContext(toggledCollapseContext);

    const decoratedOnClick = useAccordionButton(eventKey, () =>{
        if(parseInt(eventKey) == toggledCollpase){
            collapsedEvent(999);
        }else{
            collapsedEvent(parseInt(eventKey));
        }
    },
    );
  
    return (
      <a
        className={className}
        style={style}
        onClick={decoratedOnClick}
      >
        {children}
      </a>
    );
  }



function KeyWordItem({address, keyNumber, collapsedEvent, style} : KeyWordItemParam) {
    return (
        <div style={style}>
            <CustomToggle eventKey={keyNumber.toString()} collapsedEvent={collapsedEvent} className="btn p-3 w-100" style={keywordItemCss}>
                <div className="d-flex">
                    <div className="me-auto">{address.State}</div>
                    <div><img src={'/icons/icon_collapse_down.png'} /></div>
                </div>
            </CustomToggle>
        </div>
    )
}

const SearchDropDownStyle : React.CSSProperties = {
    backgroundColor : '#f6f7ff',
    border : '1px solid #e7eaf8',
    width:'80%',
    display:'flex',
    justifyContent:'space-between',
    alignItems:'center',
    paddingBlock:'10px'
}

type SearchDropDownParams = {
    title : string,
    items : string[],
}

function SearchDropDown ({title, items} : SearchDropDownParams) {
    return (
        <Dropdown as={ButtonGroup} style={{width:'90%', marginTop:'12px'}}>
            <Dropdown.Toggle variant='outline'id="dropdown-custom-1" style={SearchDropDownStyle}>
                <span className="me-2">{title}</span>
                <img src="/icons/icon_dropdown.png" />
            </Dropdown.Toggle>
            <Dropdown.Menu>
                {items.map((value, index)=>(
                    <Dropdown.Item href="#/List" key={index}>{value}</Dropdown.Item>
                ))}

            </Dropdown.Menu>
        </Dropdown>
    )
}

export { 
    SearchBar,
    KeyWord,
    SearchDropDown,
};