import React, { useState } from 'react';
import { Button, ButtonGroup, Col, Dropdown, Row, Form, Card } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { SearchDropDown } from '../../pieces/forms/Searchs';
import { MeetContainer, SubHeader, Wrapper } from '../../pieces/layouts/Layouts';
import { Badge } from '../../pieces/Tag';
import './List.css';

const categoryTitleStyle : React.CSSProperties = {
    fontWeight: 500,
    fontSize : '18px'
}

function CategoryTitle(){
    return (
        <Dropdown as={ButtonGroup}>
            <Dropdown.Toggle variant='outline'id="dropdown-custom-1" style={categoryTitleStyle}>
                <span className="me-2"></span>
                <img src="/icons/icon_dropdown.png" />
            </Dropdown.Toggle>
            <Dropdown.Menu>
                <Dropdown.Item href="#/List">Test1</Dropdown.Item>
                <Dropdown.Item href="#/List">Test2</Dropdown.Item>
                <Dropdown.Item href="#/List">Test3</Dropdown.Item>
            </Dropdown.Menu>
        </Dropdown>
    )
}

const localItems = [
    '', '', '', ''
];

const users = [
    '1', '2', '3', '4', '5', '6'
];

const dates = [
    '1', '2', '3', '4', '5', '6'
];


type SearchGroupParams = {
    style : React.CSSProperties
}

function SearchGroup({style} : SearchGroupParams){
    const [paymentChecked, paymentCheckedChange] = useState(false); 
    const [couponChecked, couponCheckedChange] = useState(false);

    const checkPayment = (event: any) => {
        let checked = event.target.checked;
        paymentCheckedChange(checked);
    }

    const checkCoupon = (event: any) => {
        let checked = event.target.checked;
        couponCheckedChange(checked);
    }

    return(
        <div style={style}>
             <Row>
                <Col xs='4'>
                    <SearchDropDown title="" items={localItems} />
                </Col>
                <Col xs='4'>
                    <SearchDropDown title="" items={users} />
                </Col>
                <Col xs='4'>
                    <SearchDropDown title="" items={dates} />
                </Col>
            </Row>
            <Row style={{marginTop:'16px'}} className="align-items-center">
                <Col xs='auto'>
                    <Form.Check 
                        type="checkbox" 
                        onChange={checkPayment} 
                        style={{color: paymentChecked ? "blue" : "black" }} 
                        id="check-payment" 
                        label="" 
                    />
                </Col>
                <Col xs='auto'>
                    <Form.Check 
                        type="checkbox" 
                        onChange={checkCoupon} 
                        style={{color: couponChecked ? "blue" : "black" }} 
                        id="check-coupon" 
                        label="" 
                    />
                </Col>
                <Col className="text-end">
                    <Button variant="outline">
                        <img src="/icons/icon_sort.png" />
                         
                    </Button>
                </Col>
            </Row>
        </div>
    )
}




