function PaymentSelect({label, index, radio}) {
    const [selected, setSelected] = radio;

    const selectRadio = (event) => {
        setSelected(true);
        console.log("selected" + index);
    }

    return (
        <Form.Check className={"m-0 p-0"} type="radio" id={"radio-" + index }>
            <Form.Check.Input onChange={selectRadio} name="payment" type="radio" className="btn-check"></Form.Check.Input>
            <Form.Check.Label style={{width:'100%', paddingBlock:'18px', color: selected ? "#5D68D3" : "black", borderColor:selected ? "#5D68D3" : "#E7EAF8", background: selected ? "#DDEDFF" : '#F6F7FF'}} className="btn btn-outline">{label}</Form.Check.Label>
        </Form.Check>
    )
}




