import React, { useEffect, useState } from 'react';
import { NavItem } from 'react-bootstrap';
import { Link, useLocation } from 'react-router-dom';

function Navigation () {
    return (
        <ul className="nav flex-column">
            <li className="nav-item">
                <a className="nav-link" href={"/#/home"}>home</a>
            </li>
            <li className="nav-item">
                <a className="nav-link" href={"/#/about"}>about</a>                    
            </li>
        </ul>
    );
}

type BottomNavigationObject = {
    image : string,
    path : string,
    text : string,
}

type BottomNavigationParams = {
    items : BottomNavigationObject[]
}

function BottomNavigation ({items} : BottomNavigationParams ) {
    const location = useLocation();


    return (
        <div className="navbar fixed-bottom" style={{paddingBottom:'32px', paddingTop:'0', backgroundColor:'white'}}>
            <ul className="nav nav-justified w-100 border-top">
                { items && items.map((item, index) => (
                    <li className='nav-item' key={index}>
                        <BottomNavigationButton {...item} />
                    </li>
                ))}
            </ul>
        </div>
    )
}

function BottomNavigationButton ({...props} : BottomNavigationObject) {
    const [active, setActive] = useState('');
    const location = useLocation();

    useEffect(()=>{
        if(location.pathname == props.path){
            setActive('active');
        }else{
            setActive('');
        }
    },[location]);

    return (
        <Link className={'nav-link ' + active + (active ? " text-black" : "text-black-50")} to={props.path} style={{paddingTop:'10px', ...(!active ? {filter:'brightness(3'} : {})}}>
            <figure>
                <img src={props.image} />
                <figcaption>{props.text}</figcaption>
            </figure>
        </Link>
    )
}


export { Navigation, BottomNavigation };

export type {BottomNavigationObject, BottomNavigationParams};