import React from 'react';
import { createHashRouter, createBrowserRouter, createRoutesFromElements, Route, RouterProvider } from 'react-router-dom';
import About from '../pages/About';
import { Around } from '../pages/Around';
import { Category } from '../pages/Category/Category';
import { CategoryList } from '../pages/Category/List';
import { CategoryView } from '../pages/Category/View';
import { NotFound } from '../pages/errors/NotFound';
import { Favorite } from '../pages/Favorite';
import Home from '../pages/Home';
import { MyPage } from '../pages/MyPage';
import { ReservationLayout } from '../pages/Reservation';
import { PaymentCheck } from '../pages/Reservation/PaymentCheck';
import { PaymentPreview } from '../pages/Reservation/PaymentPreview';
import { Reservation } from '../pages/Reservation/Reservation';
import { Search } from '../pages/Search';
import { Root } from './root';

function Router(){

  const router = createHashRouter(
    (
        createRoutesFromElements(
            <Route path="/" element={<Root />} errorElement={<NotFound />}>
                <Route path="home" element={<Home/>} />
                <Route path="about" element={<About/>} />
                <Route path="search" element={<Search />} />
                <Route path="around" element={<Around />} />
                <Route path="favorite" element={<Favorite />} />
                <Route path='mypage' element={<MyPage />} />
                
                <Route path="Category" element={<Category />}>
                    <Route path="List" element={<CategoryList />} />
                    <Route path="View" element={<CategoryView />} />
                </Route>
                <Route path="Reservation" element={<ReservationLayout />}>
                    <Route path="Reservation" element={<Reservation />} />
                    <Route path="PaymentPreview" element={<PaymentPreview />} />
                    <Route path="PaymentCheck" element={<PaymentCheck />} />
                </Route>
            </Route>
        )
    )
  );

    return (
        <RouterProvider router={router} />
    )
}

export { Router };