function ButtonToTop() {
    const [ScrollY, setScrollY] = useState(0);

    const handleFollow = () => {
        setScrollY(window.pageYOffset);
    }

    const goTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }

    useEffect(()=>{
        const watch = () => {
            window.addEventListener('scroll', handleFollow);
          }
          watch();
          return () => {
            window.removeEventListener('scroll', handleFollow);
          }
    })

    return (
        <div style={{position:'fixed', bottom:'120px', right:'30px', zIndex:99999}}>
            <Button className="justify-content-center align-items-center" onClick={goTop} variant="outline" style={{width:'45px', height:'45px', borderRadius:'45px', backgroundColor:'#3FD0C8', display: ScrollY > 100 ? 'block' : 'none'}}>
                <img src="/icons/icon_to_top.png" />
            </Button>
        </div>
    )
}

