import React from 'react';
import { Button, Image } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';

type TagParam = {
    image : string,
    txt : string
}

const TagCss : React.CSSProperties = {
    borderColor: '#DBE3EF',
    borderRadius : '20px',
    paddingBlock:'5px',
    paddingInline : '13px',
    verticalAlign:'middle'
} 

function Tag({image, txt} : TagParam){
    return (
    <>
        <input type="checkbox" className='btn-check' id="Tag" autoComplete='off' />
        <Form.Label className="btn btn-outline-secondary" style={TagCss} htmlFor="Tag">
            <Image src={image} className={'align-middle'} style={{width:'10px', height:'10px'}} />&nbsp;
            <span className={'align-middle'}>{txt}</span>
        </Form.Label>
    </>
    )
}

type BadgeParams = {
    style? : React.CSSProperties,
    bg? : string,
    color? : string,
    children : string
}

const BadgeCss : React.CSSProperties = {
    paddingInline:'6px',
    paddingBlock:'2px',
    borderRadius:'3px',
    lineHeight:'17px',
    fontSize:'12px',
    fontWeight:500
}

function Badge({style, bg, color, children} : BadgeParams){
    return (
        <span style={{...BadgeCss, ...style, backgroundColor:bg, color:color}}>
            {children}
        </span>
    )
}

export { Tag, Badge };